
























import { Component, Prop, Vue } from 'vue-property-decorator'

@Component
export default class Result extends Vue {
  @Prop() private config!: Object
  message: String = ''
  showSnackbar: Boolean = false

  handleSuccess (): void {
    this.message = '复制成功'
    this.showSnackbar = true
  }

  handleError (): void {
    this.message = '复制失败'
    this.showSnackbar = true
  }
}

